import React from 'react';

export class Individual extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      vote: 0
    }
  }
  onVote = () => {
    this.setState((prevState) => {
      return {
        vote: prevState.vote + 1
      }
    })
  }
  render(){
    var style = {
      "text-align": "center"
    };
    return(
      <tr>
        <td>{this.state.vote}</td>
        <td>{this.props.name}</td>
        <td><button type="button" onClick={this.onVote}>Vote</button></td>
      </tr>
    )
  }
}
