import React from 'react';
import { Individual } from "./Individual"

export class Vote extends React.Component {
  constructor(props){
    super(props)
    this.state = {}
  }

  render(){
    return(
      <div class="container">
        <table class="table">
          <tbody>
            <Individual name="Superman" />
            <Individual name="Batman" />
            <Individual name="Ironman" />
            <Individual name="Aquaman" />
          </tbody>
        </table>
      </div>
    )
  }
}
